<template>
  <view class="exam-container">
    <!-- È°∂ÈÉ®ËøõÂ∫¶Ê†è -->
    <view class="exam-header">
      <view class="header-info">
        <view class="back-btn" @click="handleBack">
          <uni-icons type="back" size="20" color="#333" />
        </view>
        <view class="exam-title">
          <text class="title-text">{{ titleText }}</text>
          <text class="subtitle-text">{{ subtitleText }}</text>
        </view>
        <view class="stats-btn" @click="showStats">
          <uni-icons type="bars" size="20" color="#333" />
        </view>
      </view>
      <view class="progress-container">
        <view class="progress-bar">
          <view class="progress-fill" :style="{width: progressPercent + '%'}"></view>
        </view>
        <text class="progress-text">{{ progressPercent }}%</text>
      </view>
    </view>

    <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
    <view v-if="loading" class="loading-state">
      <uni-icons type="spinner-cycle" size="40" color="#667eea" class="loading-icon" />
      <text class="loading-text">Ê≠£Âú®Âä†ËΩΩÈ¢òÁõÆ...</text>
    </view>

    <!-- È¢òÁõÆÂÜÖÂÆπ -->
    <view v-else-if="currentQuestion" class="question-content">
      <!-- È¢òÁõÆÂç°Áâá -->
      <view class="question-card">
        <view class="question-header">
          <view class="question-type" :class="'type-' + currentQuestion.type">
            <text class="type-text">{{ getTypeLabel(currentQuestion.type) }}</text>
          </view>
          <view class="question-difficulty">
            <uni-icons 
              v-for="i in 3" 
              :key="i"
              type="star-filled" 
              :size="14" 
              :color="i <= currentQuestion.difficulty ? '#ffc107' : '#ddd'" 
            />
          </view>
        </view>

        <view class="question-number">
          <text>È¢òÂè∑ {{ currentQuestionNumber }}</text>
        </view>

        <view class="question-text" @click="handleImageClick">
          <rich-text :nodes="parsedContent"></rich-text>
        </view>

        <!-- Ê†áÁ≠æ -->
        <view v-if="currentQuestion.tags && currentQuestion.tags.length > 0" class="question-tags">
          <view class="tag" v-for="(tag, index) in currentQuestion.tags" :key="index">
            <text class="tag-text">{{ tag }}</text>
          </view>
        </view>
      </view>

      <!-- ÈÄâÈ°πÂàóË°® -->
      <view class="options-list">
        <view 
          v-for="(option, index) in currentQuestion.options" 
          :key="index"
          class="option-item"
          :class="{
            'selected': isOptionSelected(index),
            'correct': showAnswer && isCorrectOption(index),
            'wrong': showAnswer && isOptionSelected(index) && !isCorrectOption(index),
            'disabled': showAnswer
          }"
          @click="selectOption(index)"
        >
          <view class="option-label">
            <text class="label-text">{{ getOptionLabel(index) }}</text>
          </view>
          <view class="option-content">
            <text class="option-text">{{ option }}</text>
          </view>
          <view class="option-icon">
            <uni-icons 
              v-if="showAnswer && isCorrectOption(index)"
              type="checkmarkempty" 
              size="20" 
              color="#28a745" 
            />
            <uni-icons 
              v-else-if="showAnswer && isOptionSelected(index) && !isCorrectOption(index)"
              type="closeempty" 
              size="20" 
              color="#dc3545" 
            />
            <view 
              v-else-if="isOptionSelected(index)" 
              class="selected-dot"
            ></view>
          </view>
        </view>
      </view>

      <!-- Á≠îÊ°àËß£Êûê -->
      <view v-if="showAnswer" class="answer-section">
        <view class="answer-header">
          <uni-icons type="info" size="20" color="#667eea" />
          <text class="answer-title">Á≠îÊ°àËß£Êûê</text>
        </view>
        <view class="answer-content">
          <view class="answer-row">
            <text class="answer-label">Ê≠£Á°ÆÁ≠îÊ°àÔºö</text>
            <text class="answer-value correct">{{ formatAnswer(currentQuestion.answer) }}</text>
          </view>
          <view class="answer-row">
            <text class="answer-label">‰Ω†ÁöÑÁ≠îÊ°àÔºö</text>
            <text class="answer-value" :class="isAnswerCorrect ? 'correct' : 'wrong'">
              {{ userAnswer || 'Êú™‰ΩúÁ≠î' }}
            </text>
          </view>
          <view class="explanation" @click="handleImageClick">
            <text class="explanation-label">Ëß£ÊûêÔºö</text>
            <view class="explanation-text">
              <rich-text v-if="currentQuestion.explanation" :nodes="parsedExplanation"></rich-text>
              <text v-else style="color:#999;">ÊöÇÊó†Ëß£Êûê</text>
            </view>
          </view>
        </view>
      </view>

      <!-- Êìç‰ΩúÊåâÈíÆ -->
      <view class="action-buttons">
        <button 
          class="action-btn secondary"
          :disabled="!hasPrevQuestion && (practiceMode === 'chapter' || !canSwitchToPrevChapter())"
          @click="prevQuestion"
        >
          <uni-icons type="back" size="16" color="#fff" />
          <text class="btn-text">‰∏ä‰∏ÄÈ¢ò</text>
        </button>

        <button 
          v-if="!showAnswer"
          class="action-btn primary show-answer"
          @click="toggleAnswer"
        >
          <uni-icons type="eye" size="16" color="#fff" />
          <text class="btn-text">Êü•ÁúãÁ≠îÊ°à</text>
        </button>
        <button 
          v-else
          class="action-btn primary hide-answer"
          @click="toggleAnswer"
        >
          <uni-icons type="eye-slash" size="16" color="#fff" />
          <text class="btn-text">Êî∂Ëµ∑</text>
        </button>

        <button 
          v-if="hasNextQuestion || (practiceMode === 'full' && canSwitchToNextChapter())"
          class="action-btn secondary"
          @click="nextQuestion"
        >
          <text class="btn-text">‰∏ã‰∏ÄÈ¢ò</text>
          <uni-icons type="forward" size="16" color="#fff" />
        </button>
        <button 
          v-else
          class="action-btn finish"
          @click="finishExam"
        >
          <uni-icons type="checkmarkempty" size="16" color="#fff" />
          <text class="btn-text">ÂÆåÊàê</text>
        </button>
      </view>
    </view>

    <!-- ÁªüËÆ°ÂºπÁ™ó -->
    <uni-popup ref="statsPopup" type="center">
      <view class="stats-popup">
        <view class="popup-header">
          <text class="popup-title">Á≠îÈ¢òÁªüËÆ°</text>
          <view class="close-btn" @click="closeStats">
            <uni-icons type="closeempty" size="20" color="#999" />
          </view>
        </view>

        <view class="stats-content">
          <view class="stat-row">
            <view class="stat-item">
              <text class="stat-number primary">{{ answeredCount }}</text>
              <text class="stat-label">Â∑≤Á≠îÈ¢ò</text>
            </view>
            <view class="stat-item">
              <text class="stat-number success">{{ correctCount }}</text>
              <text class="stat-label">Ê≠£Á°ÆÊï∞</text>
            </view>
            <view class="stat-item">
              <text class="stat-number danger">{{ wrongCount }}</text>
              <text class="stat-label">ÈîôËØØÊï∞</text>
            </view>
          </view>

          <view class="accuracy-row">
            <text class="accuracy-label">Ê≠£Á°ÆÁéá</text>
            <text class="accuracy-value">{{ accuracy }}%</text>
          </view>
          
          <view class="stats-note">
            <text class="note-text">üí° ÁªüËÆ°Êï∞ÊçÆÂü∫‰∫éÊú¨Ê¨°‰ºöËØùÁ≠îÈ¢òÊÉÖÂÜµ</text>
          </view>
        </view>

        <view class="popup-buttons">
          <button class="popup-btn secondary" @click="resetProgress">ÈáçÊñ∞ÁªÉ‰π†</button>
          <button class="popup-btn primary" @click="closeStats">ÁªßÁª≠ÁªÉ‰π†</button>
        </view>
      </view>
    </uni-popup>
  </view>
</template>

<script setup>
import { ref, computed, onMounted, onUnmounted, watch } from 'vue'
import { get, post, del } from '@/utils/request.js'
import { parseQuestionImages, extractAllQuestionImages } from '@/utils/imageParser.js'
import { API_CONFIG } from '@/utils/constants.js'

// È°µÈù¢ÂèÇÊï∞
const bankId = ref(0)
const practiceMode = ref('full') // 'chapter' | 'full'
const startChapterId = ref(null)
const startQuestionNumber = ref(1)

// È¢òÂ∫ì‰ø°ÊÅØ
const bankInfo = ref({
  bank_name: '',
  total_questions: 0
})

// Á´†ËäÇÊï∞ÊçÆ
const chapters = ref([]) // ÊâÄÊúâÁ´†ËäÇÂàóË°®
const currentChapterIndex = ref(0) // ÂΩìÂâçÁ´†ËäÇÁ¥¢Âºï
const currentChapter = ref(null) // ÂΩìÂâçÁ´†ËäÇÂØπË±°

// ÂΩìÂâçÈ¢òÁõÆ
const currentQuestionNumber = ref(1) // ÂΩìÂâçÈ¢òÂè∑ÔºàÁ´†ËäÇÂÜÖÔºå‰ªé1ÂºÄÂßãÔºâ
const currentQuestion = ref(null) // ÂΩìÂâçÈ¢òÁõÆÂØπË±°
const totalInChapter = ref(0) // ÂΩìÂâçÁ´†ËäÇÊÄªÈ¢òÊï∞
const hasNextQuestion = ref(true) // ÊòØÂê¶Êúâ‰∏ã‰∏ÄÈ¢ò
const hasPrevQuestion = ref(true) // ÊòØÂê¶Êúâ‰∏ä‰∏ÄÈ¢ò

// Á≠îÈ¢òÊï∞ÊçÆ
const userAnswers = ref({}) // {chapterId_questionNumber: answer}
const showAnswer = ref(false)
const questionCache = ref({}) // È¢òÁõÆÁºìÂ≠ò {chapterId_questionNumber: question}

// Âä†ËΩΩÁä∂ÊÄÅ
const loading = ref(false)

// ÂºπÁ™óÂºïÁî®
const statsPopup = ref(null)

// Á≠îÊ°àkeyÊ†ºÂºèÔºöchapterId_questionNumber
const getAnswerKey = () => {
  if (!currentChapter.value) return ''
  return `${currentChapter.value.id}_${currentQuestionNumber.value}`
}

// Áî®Êà∑ÂΩìÂâçÈ¢òÁõÆÁöÑÁ≠îÊ°à
const userAnswer = computed(() => userAnswers.value[getAnswerKey()] || '')

// Ê†áÈ¢òÊñáÊú¨
const titleText = computed(() => {
  if (practiceMode.value === 'chapter') {
    return currentChapter.value?.chapter_name || bankInfo.value.bank_name
  }
  return bankInfo.value.bank_name
})

// ÂâØÊ†áÈ¢òÊñáÊú¨
const subtitleText = computed(() => {
  if (practiceMode.value === 'chapter') {
    return `Á¨¨ ${currentQuestionNumber.value} / ${totalInChapter.value} È¢ò`
  }
  
  // Êï¥Âç∑Ê®°ÂºèÊòæÁ§∫ÊÄªËøõÂ∫¶
  let position = 0
  chapters.value.forEach((chapter, index) => {
    if (index < currentChapterIndex.value) {
      position += chapter.question_count
    }
  })
  position += currentQuestionNumber.value
  
  const chapterName = currentChapter.value?.chapter_name || ''
  return `Á¨¨ ${position} / ${bankInfo.value.total_questions} È¢ò (${chapterName})`
})

// ËøõÂ∫¶ÁôæÂàÜÊØî
const progressPercent = computed(() => {
  if (practiceMode.value === 'chapter') {
    return totalInChapter.value > 0 ? Math.round((currentQuestionNumber.value / totalInChapter.value) * 100) : 0
  }
  
  // Êï¥Âç∑Ê®°ÂºèËÆ°ÁÆóÊï¥‰ΩìËøõÂ∫¶
  let totalQuestions = 0
  let currentPosition = 0
  
  chapters.value.forEach((chapter, index) => {
    totalQuestions += chapter.question_count
    if (index < currentChapterIndex.value) {
      currentPosition += chapter.question_count
    }
  })
  currentPosition += currentQuestionNumber.value
  
  return totalQuestions > 0 ? Math.round((currentPosition / totalQuestions) * 100) : 0
})

// Â∑≤Á≠îÈ¢òÊï∞
const answeredCount = computed(() => Object.keys(userAnswers.value).length)

// Ê≠£Á°ÆÊï∞
const correctCount = computed(() => {
  return Object.keys(userAnswers.value).filter(key => {
    const userAns = userAnswers.value[key]
    return checkAnswerByKey(key, userAns)
  }).length
})

// ÈîôËØØÊï∞
const wrongCount = computed(() => answeredCount.value - correctCount.value)

// Ê≠£Á°ÆÁéá
const accuracy = computed(() => {
  if (answeredCount.value === 0) return 0
  return Math.round(correctCount.value / answeredCount.value * 100)
})

// ÂΩìÂâçÁ≠îÊ°àÊòØÂê¶Ê≠£Á°Æ
const isAnswerCorrect = computed(() => {
  if (!userAnswer.value) return false
  return userAnswer.value === formatAnswer(currentQuestion.value.answer)
})

// Ëß£ÊûêÂêéÁöÑÈ¢òÁõÆÂÜÖÂÆπÔºàÂåÖÂê´ÂõæÁâáÔºâ
const parsedContent = computed(() => {
  if (!currentQuestion.value || !currentQuestion.value.content) return ''
  const baseUrl = API_CONFIG.BASE_URL.replace('/api', '')
  return parseQuestionImages(currentQuestion.value.content, bankId.value, baseUrl)
})

// Ëß£ÊûêÂêéÁöÑÁ≠îÊ°àËß£ÊûêÔºàÂåÖÂê´ÂõæÁâáÔºâ
const parsedExplanation = computed(() => {
  if (!currentQuestion.value || !currentQuestion.value.explanation) return ''
  const baseUrl = API_CONFIG.BASE_URL.replace('/api', '')
  return parseQuestionImages(currentQuestion.value.explanation, bankId.value, baseUrl)
})

// Ëé∑ÂèñÂΩìÂâçÈ¢òÁõÆÊâÄÊúâÂõæÁâáURLÔºàÁî®‰∫éÈ¢ÑËßàÔºâ
const currentImageUrls = computed(() => {
  if (!currentQuestion.value) return []
  const baseUrl = API_CONFIG.BASE_URL.replace('/api', '')
  return extractAllQuestionImages(currentQuestion.value, bankId.value, baseUrl)
})

// È°µÈù¢Âä†ËΩΩ
onMounted(async () => {
  // Ëé∑ÂèñË∑ØÁî±ÂèÇÊï∞
  const pages = getCurrentPages()
  const currentPage = pages[pages.length - 1]
  const options = currentPage.options
  
  bankId.value = parseInt(options.bankId) || 0
  practiceMode.value = options.mode || 'full'
  startChapterId.value = parseInt(options.chapterId) || null
  startQuestionNumber.value = parseInt(options.questionNumber) || 1
  
  if (!bankId.value) {
    uni.showToast({ title: 'ÂèÇÊï∞ÈîôËØØ', icon: 'none' })
    setTimeout(() => {
      uni.navigateBack()
    }, 1500)
    return
  }
  
  await initExam()
  
  // ÁõëÂê¨Â∞èÁ®ãÂ∫èÈöêËóè‰∫ã‰ª∂
  uni.onAppHide(() => {
    saveProgress()
  })
})

// È°µÈù¢Âç∏ËΩΩÊó∂‰øùÂ≠òËøõÂ∫¶
onUnmounted(() => {
  saveProgress()
})

// ÁõëÂê¨È¢òÂè∑ÂíåÁ´†ËäÇÂèòÂåñÔºåËá™Âä®‰øùÂ≠òËøõÂ∫¶
watch([currentQuestionNumber, currentChapterIndex], ([newQuestionNum, newChapterIdx], [oldQuestionNum, oldChapterIdx]) => {
  if ((oldQuestionNum !== undefined && newQuestionNum !== oldQuestionNum) ||
      (oldChapterIdx !== undefined && newChapterIdx !== oldChapterIdx)) {
    saveProgress()
  }
})

// ÂàùÂßãÂåñËÄÉËØï
const initExam = async () => {
  loading.value = true
  try {
    console.log('üìñ ÂºÄÂßãÂàùÂßãÂåñËÄÉËØïÔºåÂèÇÊï∞:', {
      bankId: bankId.value,
      mode: practiceMode.value,
      chapterId: startChapterId.value,
      questionNumber: startQuestionNumber.value
    })
    
    // 1. Ëé∑ÂèñÈ¢òÂ∫ìÂü∫Êú¨‰ø°ÊÅØÔºà‰ΩøÁî®Êñ∞ÁöÑÈ¢òÂ∫ìÁÆ°ÁêÜAPIÔºâ
    const bankData = await get(`/questions/banks/${bankId.value}`, {}, { showLoading: false })
    console.log('‚úÖ È¢òÂ∫ì‰ø°ÊÅØ:', bankData)
    
    bankInfo.value = {
      bank_name: bankData.name || 'È¢òÂ∫ì',
      total_questions: bankData.question_count || 0
    }
    
    // 2. Ëé∑ÂèñÁ´†ËäÇÂàóË°®
    const chaptersData = await get(`/question-banks/${bankId.value}/chapters`, {}, { showLoading: false })
    console.log('‚úÖ Á´†ËäÇÂàóË°®:', chaptersData)
    
    chapters.value = chaptersData.chapters || []
    
    if (chapters.value.length === 0) {
      uni.showToast({ title: 'ËØ•È¢òÂ∫ìÊöÇÊó†Á´†ËäÇ', icon: 'none' })
      setTimeout(() => uni.navigateBack(), 1500)
      return
    }
    
    // 3. Á°ÆÂÆöËµ∑ÂßãÁ´†ËäÇ
    if (startChapterId.value) {
      const index = chapters.value.findIndex(c => c.id === startChapterId.value)
      currentChapterIndex.value = index >= 0 ? index : 0
      console.log(`üìç ÊâæÂà∞Ëµ∑ÂßãÁ´†ËäÇÔºåÁ¥¢Âºï: ${currentChapterIndex.value}`)
    } else {
      currentChapterIndex.value = 0
      console.log('üìç ‰ΩøÁî®Á¨¨‰∏Ä‰∏™Á´†ËäÇ')
    }
    
    currentChapter.value = chapters.value[currentChapterIndex.value]
    currentQuestionNumber.value = startQuestionNumber.value
    
    console.log('üìç ÂΩìÂâçÁ´†ËäÇ:', currentChapter.value)
    console.log('üìç Ëµ∑ÂßãÈ¢òÂè∑:', currentQuestionNumber.value)
    
    // 4. Âä†ËΩΩËµ∑ÂßãÈ¢òÁõÆ
    await loadQuestion()
    
    console.log(`‚úÖ ÂàùÂßãÂåñÂÆåÊàêÔºåÂºÄÂßã${practiceMode.value === 'chapter' ? 'Á´†ËäÇ' : 'Êï¥Âç∑'}ÁªÉ‰π†`)
    
  } catch (error) {
    console.error('‚ùå ÂàùÂßãÂåñÂ§±Ë¥•:', error)
    uni.showToast({
      title: error.message || 'Âä†ËΩΩÂ§±Ë¥•',
      icon: 'none'
    })
    setTimeout(() => uni.navigateBack(), 1500)
  } finally {
    loading.value = false
    console.log('‚úÖ ÂàùÂßãÂåñloadingÁä∂ÊÄÅÂ∑≤ÈáçÁΩÆ')
  }
}

// Âä†ËΩΩÈ¢òÁõÆÔºàÂçïÈ¢òÊ®°ÂºèÔºâ
const loadQuestion = async () => {
  if (!currentChapter.value) {
    console.error('‚ùå currentChapter is null')
    return
  }
  
  loading.value = true
  try {
    console.log(`üìñ ÂºÄÂßãÂä†ËΩΩÈ¢òÁõÆ: È¢òÂ∫ì${bankId.value}, Á´†ËäÇ${currentChapter.value.id}, È¢òÂè∑${currentQuestionNumber.value}`)
    
    const response = await get(
      `/question-banks/${bankId.value}/chapters/${currentChapter.value.id}/questions`,
      { questionNumber: currentQuestionNumber.value },
      { showLoading: false } // ‰ΩøÁî®ÁªÑ‰ª∂Ëá™Â∑±ÁöÑloadingÁä∂ÊÄÅÔºå‰∏çÊòæÁ§∫Á≥ªÁªüÂä†ËΩΩÊèêÁ§∫
    )
    
    console.log('üì° È¢òÁõÆÊï∞ÊçÆÂìçÂ∫î:', response)
    
    if (response && response.question) {
      currentQuestion.value = response.question
      totalInChapter.value = response.total || 0
      hasNextQuestion.value = response.hasNext || false
      hasPrevQuestion.value = response.hasPrev || false
      
      // Êõ¥Êñ∞È¢òÂ∫ìÊÄªÈ¢òÊï∞ÔºàÊï¥Âç∑Ê®°Âºè‰∏ãÁ¥ØÂä†ÊâÄÊúâÁ´†ËäÇÈ¢òÊï∞Ôºâ
      if (practiceMode.value === 'full' && chapters.value.length > 0) {
        let totalCount = 0
        chapters.value.forEach(chapter => {
          totalCount += chapter.question_count || 0
        })
        if (totalCount > 0) {
          bankInfo.value.total_questions = totalCount
        }
      }
      
      // ÁºìÂ≠òÈ¢òÁõÆ
      const cacheKey = getAnswerKey()
      questionCache.value[cacheKey] = response.question
      
      // ÈáçÁΩÆÁ≠îÊ°àÊòæÁ§∫Áä∂ÊÄÅ
      showAnswer.value = false
      
      console.log(`‚úÖ È¢òÁõÆÂä†ËΩΩÊàêÂäü: ${currentChapter.value.chapter_name} Á¨¨${currentQuestionNumber.value}È¢ò`)
    } else {
      console.warn('‚ö†Ô∏è ÂìçÂ∫î‰∏≠Ê≤°ÊúâquestionÂ≠óÊÆµ:', response)
      // Ê≤°ÊúâÊõ¥Â§öÈ¢òÁõÆ‰∫Ü
      if (practiceMode.value === 'full' && canSwitchToNextChapter()) {
        // Êï¥Âç∑Ê®°ÂºèÔºåËá™Âä®ÂàáÊç¢Âà∞‰∏ã‰∏ÄÁ´†ËäÇ
        await switchToNextChapter()
      } else {
        uni.showToast({ title: 'Â∑≤ÊòØÊúÄÂêé‰∏ÄÈ¢ò', icon: 'none' })
      }
    }
  } catch (error) {
    console.error('‚ùå Âä†ËΩΩÈ¢òÁõÆÂ§±Ë¥•:', error)
    uni.showToast({
      title: error.message || 'Âä†ËΩΩÂ§±Ë¥•',
      icon: 'none'
    })
  } finally {
    loading.value = false
    console.log('‚úÖ loadingÁä∂ÊÄÅÂ∑≤ÈáçÁΩÆ‰∏∫false')
  }
}

// Ê£ÄÊü•ÊòØÂê¶ÂèØ‰ª•ÂàáÊç¢Âà∞‰∏ã‰∏ÄÁ´†ËäÇ
const canSwitchToNextChapter = () => {
  return currentChapterIndex.value < chapters.value.length - 1
}

// Ê£ÄÊü•ÊòØÂê¶ÂèØ‰ª•ÂàáÊç¢Âà∞‰∏ä‰∏ÄÁ´†ËäÇ
const canSwitchToPrevChapter = () => {
  return currentChapterIndex.value > 0
}

// ÂàáÊç¢Âà∞‰∏ã‰∏ÄÁ´†ËäÇ
const switchToNextChapter = async () => {
  if (canSwitchToNextChapter()) {
    currentChapterIndex.value++
    currentChapter.value = chapters.value[currentChapterIndex.value]
    currentQuestionNumber.value = 1
    
    uni.showToast({
      title: `ËøõÂÖ•${currentChapter.value.chapter_name}`,
      icon: 'none',
      duration: 1500
    })
    
    await loadQuestion()
  }
}

// ÂàáÊç¢Âà∞‰∏ä‰∏ÄÁ´†ËäÇ
const switchToPrevChapter = async () => {
  if (canSwitchToPrevChapter()) {
    currentChapterIndex.value--
    currentChapter.value = chapters.value[currentChapterIndex.value]
    currentQuestionNumber.value = currentChapter.value.question_count
    
    await loadQuestion()
  }
}

// Ëé∑ÂèñÈ¢òÂûãÊ†áÁ≠æ
const getTypeLabel = (type) => {
  const typeMap = {
    'single': 'ÂçïÈÄâÈ¢ò',
    'multiple': 'Â§öÈÄâÈ¢ò',
    'judge': 'Âà§Êñ≠È¢ò',
    'fill': 'Â°´Á©∫È¢ò'
  }
  return typeMap[type] || 'Êú™Áü•È¢òÂûã'
}

// Ëé∑ÂèñÈÄâÈ°πÊ†áÁ≠æ
const getOptionLabel = (index) => {
  return String.fromCharCode(65 + index) // A, B, C, D...
}

// Ê†ºÂºèÂåñÁ≠îÊ°à
const formatAnswer = (answer) => {
  if (!answer) return ''
  
  // Â¶ÇÊûúÊòØÂ§öÈÄâÈ¢òÔºåÁ≠îÊ°àÂèØËÉΩÊòØ "AB" Êàñ ["A", "B"]
  if (Array.isArray(answer)) {
    return answer.sort().join('')
  }
  
  // ÂçïÈÄâÈ¢òÊàñÂà§Êñ≠È¢ò
  return answer.toString().toUpperCase()
}

// Âà§Êñ≠ÈÄâÈ°πÊòØÂê¶Ë¢´ÈÄâ‰∏≠
const isOptionSelected = (optionIndex) => {
  const key = getAnswerKey()
  const answer = userAnswers.value[key]
  if (!answer) return false
  
  const label = getOptionLabel(optionIndex)
  
  if (currentQuestion.value.type === 'multiple') {
    return answer.includes(label)
  }
  
  return answer === label
}

// Âà§Êñ≠ÈÄâÈ°πÊòØÂê¶ÊòØÊ≠£Á°ÆÁ≠îÊ°à
const isCorrectOption = (optionIndex) => {
  const correctAnswer = formatAnswer(currentQuestion.value.answer)
  const label = getOptionLabel(optionIndex)
  
  if (currentQuestion.value.type === 'multiple') {
    return correctAnswer.includes(label)
  }
  
  return correctAnswer === label
}

// ÈÄâÊã©ÈÄâÈ°π
const selectOption = (optionIndex) => {
  if (showAnswer.value) return // Â∑≤ÊòæÁ§∫Á≠îÊ°àÊó∂‰∏çËÉΩÂÜçÈÄâÊã©
  
  const label = getOptionLabel(optionIndex)
  const key = getAnswerKey()
  
  if (currentQuestion.value.type === 'multiple') {
    // Â§öÈÄâÈ¢ò
    let currentAnswer = userAnswers.value[key] || ''
    
    if (currentAnswer.includes(label)) {
      // ÂèñÊ∂àÈÄâÊã©
      currentAnswer = currentAnswer.replace(label, '')
    } else {
      // Ê∑ªÂä†ÈÄâÊã©
      currentAnswer += label
    }
    
    // ÊåâÂ≠óÊØçÈ°∫Â∫èÊéíÂ∫è
    currentAnswer = currentAnswer.split('').sort().join('')
    
    userAnswers.value[key] = currentAnswer
  } else {
    // ÂçïÈÄâÈ¢òÊàñÂà§Êñ≠È¢ò
    userAnswers.value[key] = label
  }
  
  // ÈÄâÊã©ÂêéËá™Âä®ÊòæÁ§∫Á≠îÊ°à
  setTimeout(() => {
    showAnswer.value = true
  }, 300)
}

// ÂàáÊç¢Á≠îÊ°àÊòæÁ§∫
const toggleAnswer = () => {
  showAnswer.value = !showAnswer.value
}

// ‰∏ä‰∏ÄÈ¢ò
const prevQuestion = async () => {
  if (hasPrevQuestion.value) {
    // Á´†ËäÇÂÜÖÊúâ‰∏ä‰∏ÄÈ¢ò
    currentQuestionNumber.value--
    await loadQuestion()
  } else {
    // Á´†ËäÇÂÜÖÊ≤°Êúâ‰∏ä‰∏ÄÈ¢ò‰∫Ü
    if (practiceMode.value === 'full' && canSwitchToPrevChapter()) {
      // Êï¥Âç∑Ê®°ÂºèÔºåÂèØ‰ª•ËøîÂõû‰∏ä‰∏ÄÁ´†ËäÇ
      uni.showModal({
        title: 'ËøîÂõû‰∏ä‰∏ÄÁ´†ËäÇ',
        content: 'ÊòØÂê¶ËøîÂõû‰∏ä‰∏ÄÁ´†ËäÇÁöÑÊúÄÂêé‰∏ÄÈ¢òÔºü',
        success: async (res) => {
          if (res.confirm) {
            await switchToPrevChapter()
          }
        }
      })
    } else {
      uni.showToast({ title: 'Â∑≤ÊòØÁ¨¨‰∏ÄÈ¢ò', icon: 'none' })
    }
  }
}

// ‰∏ã‰∏ÄÈ¢ò
const nextQuestion = async () => {
  if (hasNextQuestion.value) {
    // Á´†ËäÇÂÜÖÊúâ‰∏ã‰∏ÄÈ¢ò
    currentQuestionNumber.value++
    await loadQuestion()
  } else {
    // Á´†ËäÇÂÜÖÊ≤°Êúâ‰∏ã‰∏ÄÈ¢ò‰∫Ü
    if (practiceMode.value === 'full' && canSwitchToNextChapter()) {
      // Êï¥Âç∑Ê®°ÂºèÔºåÂàáÊç¢Âà∞‰∏ã‰∏ÄÁ´†ËäÇ
      uni.showModal({
        title: 'Á´†ËäÇÂÆåÊàê',
        content: `„Äå${currentChapter.value.chapter_name}„ÄçÂ∑≤ÂÆåÊàê\n\nÊòØÂê¶ÁªßÁª≠‰∏ã‰∏ÄÁ´†ËäÇÔºü`,
        confirmText: 'ÁªßÁª≠',
        cancelText: 'ÈÄÄÂá∫',
        success: async (res) => {
          if (res.confirm) {
            await switchToNextChapter()
          } else {
            handleBack()
          }
        }
      })
    } else {
      // Á´†ËäÇÁªÉ‰π†Ê®°ÂºèÊàñÂ∑≤ÊòØÊúÄÂêé‰∏ÄÁ´†
      uni.showToast({ title: 'Êú¨Á´†ËäÇÂ∑≤ÂÆåÊàê', icon: 'success' })
      finishExam()
    }
  }
}

// Ë∑≥ËΩ¨Âà∞ÊåáÂÆöÈ¢òÁõÆÔºàÁÆÄÂåñÁâàÔºå‰ªÖÁî®‰∫éÁªüËÆ°ÂºπÁ™óÔºâ
const jumpToQuestion = async (questionNumber) => {
  if (questionNumber >= 1 && questionNumber <= totalInChapter.value) {
    currentQuestionNumber.value = questionNumber
    await loadQuestion()
    closeStats()
  }
}

// Ê£ÄÊü•Á≠îÊ°àÊòØÂê¶Ê≠£Á°ÆÔºà‰ΩøÁî®ÁºìÂ≠òÁöÑÈ¢òÁõÆ‰ø°ÊÅØÔºâ
const checkAnswerByKey = (answerKey, userAns) => {
  if (!userAns) return false
  
  const cachedQuestion = questionCache.value[answerKey]
  if (!cachedQuestion) return false
  
  const correctAns = formatAnswer(cachedQuestion.answer)
  return userAns === correctAns
}

// ÊòæÁ§∫ÁªüËÆ°
const showStats = () => {
  statsPopup.value.open()
}

// ÂÖ≥Èó≠ÁªüËÆ°
const closeStats = () => {
  statsPopup.value.close()
}

// ‰øùÂ≠òÂ≠¶‰π†ËøõÂ∫¶
const saveProgress = async () => {
  if (!bankId.value || !currentChapter.value) return
  
  try {
    if (practiceMode.value === 'chapter') {
      // Á´†ËäÇÁªÉ‰π†Ôºö‰øùÂ≠òÂà∞ÂÖ∑‰ΩìÁ´†ËäÇ
      await post(
        `/user-progress/${bankId.value}/chapters/${currentChapter.value.id}`,
        {
          practice_mode: 'chapter',
          current_question_number: currentQuestionNumber.value,
          completed_count: currentQuestionNumber.value,
          total_questions: totalInChapter.value
        },
        { showLoading: false }
      )
      
      console.log('üíæ Á´†ËäÇËøõÂ∫¶Â∑≤‰øùÂ≠ò:', {
        mode: 'chapter',
        chapter: currentChapter.value.chapter_name,
        questionNumber: currentQuestionNumber.value,
        completedCount: currentQuestionNumber.value,
        total: totalInChapter.value
      })
    } else {
      // Êï¥Âç∑ÁªÉ‰π†Ôºö‰øùÂ≠òÂà∞chapter_id=0ÔºåËÆ∞ÂΩïÊï¥‰ΩìËøõÂ∫¶
      // ËÆ°ÁÆóÊï¥‰ΩìÂÆåÊàêÁöÑÈ¢òÁõÆÊï∞ÔºàÂâçÈù¢Á´†ËäÇÁöÑÈ¢òÊï∞ + ÂΩìÂâçÁ´†ËäÇÁöÑÈ¢òÂè∑Ôºâ
      let totalCompleted = currentQuestionNumber.value
      for (let i = 0; i < currentChapterIndex.value; i++) {
        totalCompleted += chapters.value[i].question_count || 0
      }
      
      await post(
        `/user-progress/${bankId.value}/chapters/0`,
        {
          practice_mode: 'full',
          current_chapter_id: currentChapter.value.id,
          current_question_number: currentQuestionNumber.value,
          completed_count: totalCompleted,
          total_questions: bankInfo.value.total_questions
        },
        { showLoading: false }
      )
      
      console.log('üíæ Êï¥Âç∑ËøõÂ∫¶Â∑≤‰øùÂ≠ò:', {
        mode: 'full',
        chapter: currentChapter.value.chapter_name,
        chapterQuestionNumber: currentQuestionNumber.value,
        totalCompleted: totalCompleted,
        total: bankInfo.value.total_questions
      })
    }
  } catch (error) {
    console.error('‰øùÂ≠òËøõÂ∫¶Â§±Ë¥•:', error)
  }
}

// ÈáçÁΩÆÂ≠¶‰π†ËøõÂ∫¶
const resetProgress = async () => {
  uni.showModal({
    title: 'ÈáçÊñ∞ÁªÉ‰π†',
    content: 'Á°ÆÂÆöË¶ÅÊ∏ÖÈô§ÂΩìÂâçÁ´†ËäÇËøõÂ∫¶ÔºåÈáçÊñ∞ÂºÄÂßãÁªÉ‰π†ÂêóÔºü',
    confirmText: 'Á°ÆÂÆö',
    cancelText: 'ÂèñÊ∂à',
    success: async (res) => {
      if (res.confirm) {
        try {
          uni.showLoading({ title: 'ÈáçÁΩÆ‰∏≠...' })
          
          // Âà†Èô§ÂΩìÂâçÁ´†ËäÇËøõÂ∫¶
          if (practiceMode.value === 'chapter') {
            // Á´†ËäÇÁªÉ‰π†ÔºöÂè™Âà†Èô§ÂΩìÂâçÁ´†ËäÇ
            await del(`/user-progress/${bankId.value}/chapters/${currentChapter.value.id}`)
          } else {
            // Êï¥Âç∑ÁªÉ‰π†ÔºöÂà†Èô§ÊâÄÊúâÁ´†ËäÇËøõÂ∫¶
            await del(`/user-progress/${bankId.value}`)
          }
          
          // ÈáçÁΩÆÊú¨Âú∞Áä∂ÊÄÅ
          currentQuestionNumber.value = 1
          userAnswers.value = {}
          showAnswer.value = false
          questionCache.value = {}
          
          // ÈáçÊñ∞Âä†ËΩΩÁ¨¨‰∏ÄÈ¢ò
          await loadQuestion()
          
          uni.hideLoading()
          uni.showToast({
            title: 'Â∑≤ÈáçÊñ∞ÂºÄÂßã',
            icon: 'success'
          })
          
          console.log('üîÑ Â≠¶‰π†ËøõÂ∫¶Â∑≤ÈáçÁΩÆ')
        } catch (error) {
          uni.hideLoading()
          console.error('ÈáçÁΩÆËøõÂ∫¶Â§±Ë¥•:', error)
          uni.showToast({
            title: error.message || 'ÈáçÁΩÆÂ§±Ë¥•',
            icon: 'none'
          })
        }
      }
    }
  })
}

// ÂÆåÊàêËÄÉËØï
const finishExam = () => {
  // ‰øùÂ≠òÊúÄÁªàËøõÂ∫¶
  saveProgress()
  
  const totalQuestions = practiceMode.value === 'chapter' ? totalInChapter.value : bankInfo.value.total_questions
  
  uni.showModal({
    title: 'ÂÆåÊàêÁªÉ‰π†',
    content: `Â∑≤ÂÆåÊàê ${answeredCount.value}/${totalQuestions} È¢ò\nÊ≠£Á°ÆÁéáÔºö${accuracy.value}%`,
    confirmText: 'Êü•ÁúãÁªüËÆ°',
    cancelText: 'ËøîÂõû',
    success: (res) => {
      if (res.confirm) {
        showStats()
      } else {
        uni.navigateBack()
      }
    }
  })
}

// ËøîÂõû
const handleBack = () => {
  uni.showModal({
    title: 'Á°ÆËÆ§ÈÄÄÂá∫',
    content: 'Â≠¶‰π†ËøõÂ∫¶Â∑≤Ëá™Âä®‰øùÂ≠òÔºåÁ°ÆÂÆöË¶ÅÈÄÄÂá∫ÂêóÔºü',
    success: async (res) => {
      if (res.confirm) {
        // ÈÄÄÂá∫Ââç‰øùÂ≠ò‰∏ÄÊ¨°ËøõÂ∫¶
        await saveProgress()
        uni.navigateBack()
      }
    }
  })
}

// Â§ÑÁêÜÂõæÁâáÁÇπÂáª‰∫ã‰ª∂ÔºàÈ¢ÑËßàÂõæÁâáÔºâ
const handleImageClick = () => {
  if (currentImageUrls.value.length > 0) {
    uni.previewImage({
      urls: currentImageUrls.value,
      current: 0
    })
  }
}
